import * as http from '../../libs/http'
export default class Map {
  constructor(data) {
    Object.assign(this, this.data(data))
    this.mounted()
  }
  data(data) {
    return {
      el: data.el, // 
      datar: {},
      linesearch: null,
      polygonLine: null,
      infoWindow:null,//
      busPolyline: null,
      mapCenter: [121.460752,31.011182], // 
      overlayGroups: new AMap.OverlayGroup(),//
    
    }
  }
  // 
  mounted() {
    this.initMap()
  }
  initMap() {
    this.map = new AMap.Map(this.el, {
      zoom: 10, // 
      center: this.mapCenter, // 
      // resizeEnable: true, //
      mapStyle: 'amap://styles/d67717253a691e523956e9482ca38f1e',
      expandZoomRange: true //  20
    })
    
    this.map.add(this.overlayGroups);
    this.infoWindow = new AMap.InfoWindow({
      isCustom: true,  //
      content: '',
      offset: new AMap.Pixel(0, -35)
    });


  }

   //
   drawbusLine(BusArr,type) {
    if(this.busPolyline){
      this.map.remove(this.busPolyline)
     
    }
    let num=Math.round((BusArr.geom.length)/2)
    //
    this.busPolyline = new AMap.Polyline({
      map: this.map,
      path: BusArr.geom,
      strokeColor: "#50C0FF",//
      strokeOpacity: 0.8,//
      isOutline: true,
      outlineColor: '#50C0FF',
      showDir: true,
      lineJoin :'round',
      lineCap :'round',
      cursor:'pointer',
      strokeWeight: 10//
    });
    var marker = new AMap.Marker({ content: ' ', map: this.map });
    this.busPolyline.on('mouseover',  (e)=> {
      marker.setPosition(BusArr.geom[num]);
      marker.setLabel({ content: `<div style='box-shadow: 0px 0px 10px rgba(0, 49, 61, 0.8) inset;background:rgba(0, 49, 61, 0.6);border: 1px solid #00FFFF;padding:10px 16px;border-radius:8px;font-size:18px;color:#ffffff'>${type==1?""+BusArr.coefficient:""+BusArr.lineLength}</div>` })

    })
    this.busPolyline.on('mouseout',  (e)=> {
      marker.setLabel({ content: `` })
    })

    this.map.setFitView(this.busPolyline, true, [60, 200, 60, 60]);

  }



}